<template>
  <div class="mv">
    <div class="header" @click="back">
      <img src="../assets/images/reback.png" alt class="auto-img" />
    </div>
    <div class="m-mvbox">
      <div class="m-mvsec">
        <div class="cvr bg">
          <video
            v-if="mvData.brs"
            controls
            class="video"
            :src="mvData.brs[720]"
            preload="metadata"
            controlslist="nodownload"
            playsinline
            :poster="mvData.cover"
            ref="videoPlay"
          ></video>
          <a id="videoCover" class="videocover" v-if="isShow">
            <img class="img auto-img" :src="mvData.cover" />
            <span class="play" @click="play">
              <svg
                class="u-svg u-svg-playvd"
                xmlns="http://www.w3.org/2000/svg"
                viewBox="0 0 138 138"
              >
                <circle fill-opacity="0.2" cx="69" cy="69" r="66" />
                <circle
                  fill="none"
                  stroke="#fff"
                  stroke-width="3"
                  stroke-opacity="0.5"
                  cx="69"
                  cy="69"
                  r="67.5"
                />
                <path
                  fill="#fff"
                  d="m53 44.954c0 0-.244-4.981 4.02-2.01l39.17 24.07c0 0 4.063 1.94 0 4.01l-39.17 24.07c0 0-3.848 2.848-3.936-1.563l-.083-48.579"
                />
              </svg>
            </span>
          </a>
        </div>
      </div>
    </div>
    <div class="m-scroll">
      <div class="m-titlemv">
        <h4 class="tt">
          <p class="name f-brk">
            <i class="icon f-bd">MV</i>
            <span>{{mvData.name}}</span>
          </p>
        </h4>
        <a href="/m/artist?id=9548" class="singer">
          歌手：
          {{mvData.artistName}}
        </a>
        <p class="info">
          <span class="time f-bd">
            发布时间：
            {{mvData.publishTime}}
          </span>
          <span class="played">
            播放：
            {{translatePlayCount(mvData.playCount)}}
          </span>
        </p>
      </div>
    </div>
  </div>
</template>

<script>
import "../assets/less/mv.less";
export default {
  name:'Mv',
  data() {
    return {
      id: "",
      mvData: [],
      isShow: true,
    };
  },
  created() {
    this.id = this.$route.query.id;
    
    this.$nextTick(() => {
      this.getMvData(this.id);
    });
  },
  methods: {
    back() {
      this.$router.go(-1);
    },
    //格式化数据
    translatePlayCount(n) {
      if (n > 99999999) {
        n / 10000;
        return Number(n / 100000000).toFixed(2) + "亿";
      }
      if (n > 9999) {
        n / 10000;
        return Number(n / 10000).toFixed(1) + "万";
      }
    },
    //获取mv数据
    getMvData(id) {
      this.mvData = [];
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
        loadingType: "spinner",
      });
      this.axios({
        method: "GET",
        url: "https://api.imjad.cn/cloudmusic/?type=mv&id=" + id + "&br=128000",
      })
        .then((result) => {
          this.$toast.clear();
          
          if (result.data.code == 200) {
            this.mvData = result.data.data;
          }
        })
        .catch((err) => {
          this.$toast.clear();
        });
    },
    //播放
    play() {
      this.$refs.videoPlay.play();
      this.isShow = false;
    },
  },
};
</script>

<style lang="less" scoped>
</style>